<script>
    import User from "../User";
    import Csrf from "../Csrf";
   export default{
     name: 'Navigation',
     data(){
        return {
            isLoggedIn:false
        };
    },
    methods:{
        logout(){
            Csrf.get_cookie();
            User.logout().then(()=>{
                // localStorage.setItem('login',"false");
                // this.isLoggedIn = localStorage.getItem("login");
                this.isLoggedIn = false;
                localStorage.removeItem('auth');
